.container {
    margin: 50px 0;
}

.card {
    margin: 0 2% !important;
}

.infected {
    border-bottom: 10px solid rgb(0, 0, 225, 0.5);
}


.recovered {
    border-bottom: 10px solid rgb(0, 225, 0, 0.5);
}


.deaths {
    border-bottom: 10px solid rgb(225, 0, 0, 0.5);
}

@media (max-width: 770px) {
    .card {
        margin: 2% !important;
    }
}